.menu-item {
  background-color: #c091a0 ;
  height: auto;
   text-align: center ;
   width:80px ;
   padding:0; 
   margin: 0px 5px;
  }
  input{
    background-color: #b1859b;
    color: #e6b9ad;
    border: 2px solid #d4a1a1;
  }

  
  
  
  .ant-row.ant-form-item{
    color: #b1859b;
  
  }
  
  .ant-dropdown-link :hover{
    color: #e6caad;
  }
  ant-dropdown-link.ant-dropdown-link{
  margin: 15px 2px;
}

  .ant-form>div , .list ,.form{
    text-align: center;
    
  }
  .ant-form{
    margin: 15px 2px;
    display: flex;
    justify-content: center;
  
  }
  .ul{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  .div{
    display: flex;
    justify-content: center;
    margin: 10px;
  }
  .list-group-item.list-group-item{
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    margin: 2px;
    background-color: #c091a0;
  }
  .joke{
    text-align: center;
    background-color: #d4a1a1;
    color: #e6caad;
    width: 70vw;
    margin: 10px auto;
    border: 2px solid #c091a0;
  }
  
  .fave-joke{
    text-align: center;
    background-color: #b1859b;
    color: #e6b9ad;
    width: 70vw;
    margin: 10px auto;
    border: 2px solid #d4a1a1;
  }
  .list-group-item.list-group-item:hover{
    background-color: #d4a1a1;
  }
  .done.list-group-item{
    background-color: #d4a1a1;
  }
  .done.list-group-item:hover{
    background-color: #c091a0;
  }
  
  .list-group-item.list-group-item >input{
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    border: none;
    border-radius: 4px;
  }
  .button{
    margin: 5px;
    
  }
  .ant-menu-horizontal > .ant-menu-item > a{
     color: #e6b9ad;
  }
  .ant-menu-horizontal > .ant-menu-item > a:hover{
    color: #b1859b;
  }
  .ant-menu-horizontal > .ant-menu-item-selected >a {
    background-color: #e6b9ad;
    color: #b1859b;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
  }
.body {
  height: calc(100vh - 64px);
  width: 100vw;
  display: grid;
  grid-template-rows: 20.3vh 43.3vh 23.3vh;
  grid-template-columns: 20vw 60vw 20vw;
  grid-template-areas: 
  ' . . temp '
  ' .  time . '
  ' quote quote  quote '
  ;
  background-size: cover;
  background-position: center;
  color: whitesmoke;
  text-shadow: 2px 2px #b1859b;
  background-size: cover; 
    background-repeat: no-repeat;
}

.wether {
justify-self: center;
  grid-area: temp;
}

.temp {
  font-size: 1.5em;
  font-weight: bold;
}

.time  {
  grid-area: time;
  justify-self: center;
  align-self: center;
}

#time {
  font-size: 6em;
  font-weight: bold;
  text-align: center;
  margin: 0;
  padding: 0;
}
#greeting{
  font-size: 3em;
  font-weight: bold;
  text-align: center;
  margin: 0;
  padding: 0;
}

.quote{
  grid-area: quote;
  justify-self: center;
  align-self: center;
  font-size: 2em;
  text-align: center;
}
